@import url(https://fonts.googleapis.com/css2?family=Ubuntu&family=Holtwood+One+SC&family=Orbitron:wght@500&display=swap);
body {
  margin: 0;
  font-family: 'Ubuntu', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.logo {
  font-family: 'Holtwood One SC', serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav {
  position: fixed;
  top: 0;
  width: 100vw;
  padding-top: 20px;
  background-color: white;
  z-index: 1;
}

.navbar {
  display: flex;
  padding: 0 4em 0 6em;
  position: relative;
}

.nav-list {
  margin-left: auto;
  font-weight: bold;
  display: flex;
  list-style-type: none;
  padding-top: 11px;
}

.list-item {
  padding: 10px 2em;
  text-decoration: none;
}

.navbar-btn {
  visibility: hidden;
  position: absolute;
  z-index: 1;
}

.sidenav {
  width: 30vw;
  height: 100vh;
  z-index: 2;
  position: absolute;
  top: 0;
  right: 0;
  background-color: white;
  padding-top: 2em;
}

@media only screen and (max-width: 660px) {
  .nav-list {
    visibility: hidden;
  }

  .list-item {
    font-weight: bold;
  }

  .sidenav-list {
    list-style-type: none;
  }

  .sidenav-list > li {
    padding: 15px 0;
  }

  .sidenavbar-btn {
    visibility: visible;
    height: 35px;
    width: 35px;
    color: black;
    background-color: white;
    border: none;
  }

  .navbar {
    display: flex;
    padding: 0 4em 0 6em;
    position: relative;
  }

  .navbar-btn {
    visibility: visible;
    height: 35px;
    width: 35px;
    color: black;
    background-color: white;
    border: none;
    position: absolute;
    right: 40px;
  }
}

@media only screen and (min-width: 661px) {
  .sidenav {
    visibility: hidden;
  }
}

.container {
  padding: 0 6em;
  margin-top: 150px;
}

.project-thumbnails {
  display: inline-block;
}

.project-thumbnails > .thumbnail {
  margin: 10px;
}

.contact {
  margin-bottom: 10px;
}

.contact > .contact-icon {
  margin-left: 20px;
  margin-right: 20px;
  font-size: 30px;
}

footer {
  text-align: center;
  padding-top: 2em;
  padding-bottom: 1em;
}

footer > small {
  font-family: 'Orbitron', sans-serif;
  font-size: 11px;
}

.project {
  margin-bottom: 50px;
}

@media only screen and (max-width: 590px) {
  .container {
      padding: 0 40px;
  }

  .navbar {
    padding-left: 40px;
  }
}

@media only screen and (max-width: 750px) {
  .project {
    overflow-x: scroll;
  }
}

@media only screen and (max-width: 365px) {
  .container {
    padding: 0 30px;
  }

  .sidenav {
    width: 40vw;
  }
}
.about {
    padding: 0 50px;
    margin-bottom: 100px;
}

.about-card {
    display: flex;
}

.about-me {
    width: 40vw;
    display: block;
}

.myimg {
    width: 30vw;
    overflow: hidden;
    display: block;
}

.myimg > img {
    border-radius: 15px;
}

.about-me > h3 {
    margin-bottom: 50px;
}

.about-me > p {
    line-height: 30px;
}

.info-card {
    margin-top: 50px;
    padding: 20px;
    border-radius: 15px;
}

.infoSize {
    font-size: 20px;
    line-height: 10px;
}

.stu {
    background-color: #d2f8d2;
    color: #037d50;
}

.exp {
    background-color: #ffffd4;
    color: orangered;
}

.int {
    background-color: #ddd3ee;
    color: #8565c4;
}

.info-card > h2 {
    margin-bottom: 60px;
}

.infoSize > p {
    margin-bottom: 50px;
}

.info-card-container {
    display: grid;
    grid-template-columns: auto auto auto;
    text-align: center;
    grid-gap: 30px;
    gap: 30px;
}

@media only screen and (max-width: 970px) {
    .about-card {
        display: block;
        text-align: center;
    }

    .about-me {
        width: 100%;
    }

    .myimg {
        width: 100%;
    }
}

@media only screen and (max-width: 700px) {
    .info-card-container {
        width: 100%;
        display: block;
    }
}

@media only screen and (max-width: 590px) {
    .about {
        padding: 0;
    }
}

@media only screen and (max-width: 1020px) {
    .info-card-container {
        display: block;
    }
}
.service {
    margin-bottom: 100px;
}

.icon-header {
    display: flex;
    align-items: center;
}

.icon-header > img {
    margin-right: 20px;
}

.service-card {
    padding: 30px;
}

.purple {
    background-color: #e9e3f4;
    color: #7000a0;
}

.yellow {
    background-color: #ffffd4;
    color: orangered;
}

.service-card-container {
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 30px;
    gap: 30px;
}

.skills-container {
    margin-top: 70px;
}

.skills {
    background-color: #e8f4f8;
    border-left: 15px solid blue;
    padding: 30px 50px;
}

.skills-header > hr {
    border: 3px solid blue;
    width: 50px;
    margin: 0 0 20px 0;
    border-radius: 20px;
}

.skills-header > h2 {
    margin-bottom: 12px;
}

.progress {
    background-color: #e0e0de;
    border-radius: 50px;
    height: 18px;
}

.progress-bar {
    padding: 5px;
    color: white;
    font-weight: bold;
}

.progress-container {
    margin-top: 50px;
}

.progress-container > h3 {
    margin-bottom: 10px;
}

@media only screen and (max-width: 660px) {
    .service-card-container {
        display: block;
    }

    .service-card {
        margin-bottom: 20px;
    }
}

@media only screen and (max-width: 400px) {
    .skills {
        padding: 50px 25px;
    }

    .progress-container > div > h3 {
        font-size: 15px;
    }
}
.image-view {
    white-space: nowrap;
    overflow-x: scroll;
    overflow-y: hidden;
}

.image-view::-webkit-scrollbar {
    display: none;
}

.photo-container {
    display: inline;
    margin-right: 30px;
}

.project-info {
    margin-top: 70px;
    padding: 40px 60px 60px;
    background-color: #e0e0de;
    border-left: 10px solid #6317a9;
}

.project-info > h1 {
    margin-bottom: 50px;
}

@media only screen and (max-width: 600px) {
    .project-info {
        padding: 30px 40px 40px;
    }
}

@media only screen and (max-width: 370px) {
    .project-info {
        padding: 20px 30px 30px;
    }

    .project-info > h1 {
        font-size: 25px;
    }
}
